'use client'

import { useUser } from '@/core/hooks/useUser'

export default function UserPage() {
  const { user, isLoading } = useUser()

  return (
    <>
      {isLoading && <p>Loading...</p>}

      {user ? (
        <div>
          <h2>{user.name}</h2>
          <p>{user.email}</p>
          <pre>{JSON.stringify(user, null, 2)}</pre>
        </div>
      ) : (
        <div>No user</div>
      )}
    </>
  )
}
